.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  white-space: nowrap;
  border: 0;
}
@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/muller-regular.woff2") format("woff2"), url("../fonts/muller-regular.woff") format("woff");
}
@font-face {
  font-family: "Muller";
  font-style: bold;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/muller-medium.woff2") format("woff2"), url("../fonts/muller-medium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #000;
  font-family: "Muller", sans-serif;
  background-color: #fff;
}
@keyframes ring {
  0% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0);
  }
}
.wrapper {
  min-width: 320px;
  max-width: 1880px;
  margin: 0 auto;
}
.button {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #000;
  display: block;
  min-width: 250px;
  padding: 18px 30px 15px;
  text-align: center;
  color: #fff;
  background-color: #f9572e;
  border: none;
  border-radius: 25px;
  box-shadow: 0 7px 15px rgba(249, 87, 46, 0.6);
  transition-duration: 200ms;
}
@media (min-width: 1440px) {
  .button:not(:disabled):hover,
  .button:not(:disabled):focus {
    cursor: pointer;
    background-color: #ff7b59;
    outline: none;
    box-shadow: 0 15px 20px rgba(249, 87, 46, 0.7);
    transition-timing-function: linear;
  }
  .button:not(:disabled):active {
    box-shadow: 0 7px 15px rgba(249, 87, 46, 0.6);
    transform: translateY(2px);
    transition-duration: 200ms;
    transition-timing-function: linear;
  }
}
.toast {
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
  width: 100%;
  height: 0;
  padding-top: 10px;
  padding-left: 10px;
}
.toast__item {
  display: inline-flex;
  padding: 5px 10px;
  margin-bottom: 5px;
  background-color: #b61500;
  border-radius: 5px;
}
