    max-width: 100%;
    text-align: center;
  }
}
.greeting__link--scroll {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #f9572e;
  position: relative;
  min-height: 36px;
  padding-left: 58px;
  margin: auto 0 10px;
}
.greeting__link--scroll::before,
.greeting__link--scroll::after {
  position: absolute;
  content: "";
  border-right: 2px solid #f9572e;
  border-bottom: 2px solid #f9572e;
  border-radius: 0 0 4px 0;
  transform: rotate(45deg);
  transition-duration: 200ms;
  transition-timing-function: linear;
}
.greeting__link--scroll::before {
  top: -3px;
  left: 18px;
  width: 15px;
  height: 15px;
}
.greeting__link--scroll::after {
  top: 0;
  left: 10px;
  width: 30px;
  height: 30px;
}
@media (min-width: 1440px) {
  .greeting__link--scroll:hover::before,
  .greeting__link--scroll:focus::before,
  .greeting__link--scroll:hover::after,
  .greeting__link--scroll:focus::after {
    transform: rotate(45deg) skew(8deg, 8deg);
  }
  .greeting__link--scroll:active::before,
  .greeting__link--scroll:active::after {
    border-color: #ff7b59;
    transform: rotate(45deg) skew(8deg, 8deg) translate(2px, 2px);
  }
}
