  align-self: center;
  margin: 0;
}
.subscribe__privacy {
  text-decoration: none;
  color: inherit;
  outline: none;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  color: #f9572e;
  align-self: center;
}
@media (min-width: 1440px) {
  .subscribe__privacy:hover,
  .subscribe__privacy:focus {
    color: #ff7b59;
  }
}
.subscribe__success {
  position: relative;
  padding: 200px 105px 30px;
  margin: auto;
  letter-spacing: 0.65px;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .subscribe__success {
    padding: 200px 0 30px;
  }
}
.subscribe__success::before,
.subscribe__success::after {
  position: absolute;
  top: 0;
  left: 50%;
  height: 20px;
  content: "";
  background-color: #f9572e;
  border-radius: 10px;
}
.subscribe__success::before {
  width: 113px;
  transform: translate(-120px, 116px) rotate(45deg);
}
.subscribe__success::after {
  width: 201px;
  transform: translate(-67px, 85px) rotate(-45deg);
}
.footer {
  background-color: #f2f2f2;
}
.footer__wrapper {
  padding: 72px 80px 0;
}
@media (max-width: 767px) {
  .footer__wrapper {
    padding: 30px 10px 0;
  }
}
.footer__copyright {
  padding: 32px 20px 29px;
  margin: 0;
  text-align: center;
  color: #666;
  border-top: 1px solid #ccc;
}
@media (max-width: 767px) {
  .footer__copyright {
    padding: 15px 20px 15px;
  }
}
.socials {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  max-width: 984px;
  margin: 0 auto 72px;
}
@media (max-width: 767px) {
  .socials {
    margin-bottom: 30px;
  }
}
.socials__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  justify-content: center;
  align-items: center;
  row-gap: 10px;
}
.socials__list--networks {
  margin: 0 0 33px;
  column-gap: 30px;
  grid-template-columns: repeat(auto-fit, 51px);
}
@media (max-width: 767px) {
  .socials__list--networks {
    margin-bottom: 20px;
    column-gap: 10px;
  }
}
.socials__list--contacts {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
}
.socials__list--contacts .socials__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 20px;
}
@media (max-width: 767px) {
  .socials__list--contacts {
    grid-auto-rows: minmax(52px, auto);
  }
}
.socials__tagline {
  font-size: 21px;
  font-weight: 400;
  line-height: 26px;
  color: #000;
  margin: 0 auto 4px;
  text-align: center;
}
@media (max-width: 767px) {
  .socials__tagline {
    padding: 0 25px;
  }
}
.socials__description {
  max-width: 450px;
  margin: 0 auto 76px;
  text-align: center;
  color: #666;
}
@media (max-width: 767px) {
  .socials__description {
    margin-bottom: 30px;
  }
}
.socials__icon {
  text-decoration: none;
  color: inherit;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 51px;
  height: 51px;
  background-color: #f9572e;
  transition-duration: 200ms;
}
@media (min-width: 1440px) {
  .socials__icon[href]:hover,
  .socials__icon[href]:focus {
    background-color: #ff7b59;
    box-shadow: 0 15px 20px rgba(249, 87, 46, 0.7);
    transition-timing-function: linear;
  }
  .socials__icon[href]:active {
    box-shadow: 0 7px 15px rgba(249, 87, 46, 0.6);
    transform: translateY(2px);
    transition-duration: 200ms;
    transition-timing-function: linear;
  }
}
.socials__contact {
  text-decoration: none;
  color: inherit;
  outline: none;
  font-size: 21px;
  font-weight: 400;
  line-height: 30px;
  color: #000;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
}
.socials__contact svg {
  flex-shrink: 0;
}
.socials__contact--phone,
.socials__contact--address,
.socials__contact--email {
  white-space: nowrap;
}
.socials__contact--phone {
  margin-top: 9px;
}
.socials__contact--phone svg {
  margin: 0 6px 6px 0;
}
@media (min-width: 1440px) {
  .socials__contact--phone[href]:hover svg,
  .socials__contact--phone[href]:focus svg {
    animation-name: ring;
    animation-duration: 100ms;
    animation-iteration-count: 5;
    animation-direction: alternate;
  }
}
.socials__contact--address {
  flex-direction: column;
  margin-right: 5px;
}
.socials__contact--address .socials__city-name {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #666;
  margin-top: -2px;
  margin-left: 4px;
}
.socials__contact--email {
  margin-top: 10px;
}
.socials__contact--email span {
  border-bottom: 2px solid #f9572e;
}
.socials__contact--email svg {
  margin: 0 9px 5px 0;
}
@media (min-width: 1440px) {
  .socials__contact[href]:hover,
  .socials__contact[href]:focus {
    color: #f9572e;
  }
  .socials__contact[href]:active {
    opacity: 0.7;
  }
}
@media (max-width: 767px) {
  .socials__contact {
    margin: 0;
  }
}
